___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --primary-color: rgb(17, 86, 102);
  --primary-color-darker: rgb(9, 48, 56);
}

* {
  box-sizing: border-box;
  outline: 0;
}

body {
  margin: 0;
  padding: 0;
  background: var(--primary-color);
  font-family: 'Open sans', sans-serif;
  font-size: 1.3em;
  line-height: 1.5em;
}

.container {
  max-width: 640px;
  margin: 50px auto;
  background: #fff;
  padding: 20px;
  border-radius: 10px;
  backdrop-filter: blur(5px); /* Aplica um desfoque ao plano de fundo */
  background-color: rgba(233, 229, 229, 0.3); /* Define um fundo transparente */
  backdrop-filter: blur(10px); /* Aplica um desfoque ao plano de fundo */
  background-color: rgba(255, 255, 255, 0.3); /* Define um fundo transparente */
}
.labels {
display: grid;
gap: 5px;
}
form input, form label, form button {
  display: block;
  width: 100%;
  margin-bottom: 10px;
}

form input {
  font-size: 24px;
  height: 50px;
  padding: 0 20px;
}

form input:focus {
  outline: 1px solid var(--primary-color);
}

form button {
  border: none;
  background: var(--primary-color);
  color: #fff;
  font-size: 18px;
  font-weight: 700;
  height: 50px;
  cursor: pointer;
  margin-top: 30px;
}

form button:hover {
  background: var(--primary-color-darker);
}

.container h1 {
  text-align: center;
  margin-bottom: 40px;
}

/* Hide the default checkbox */
.container-input input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
  margin-bottom: 5px;
 }
 
 .container-input {
  display: block;
  position: relative;
  cursor: pointer;
  user-select: none;
 }
 
 /* Create a custom checkbox */
 .checkmark {
  position: relative;
  top: 0;
  left: 0;
  height: 1.3em;
  width: 1.3em;
  background: black;
  border-radius: 50px;
  transition: all 0.7s;
  --spread: 20px;
 }
 
 /* When the checkbox is checked, add a blue background */
 .container-input input:checked ~ .checkmark {
  background: black;
  box-shadow: -10px -10px var(--spread) 0px #5B51D8, 0 -10px var(--spread) 0px #833AB4, 10px -10px var(--spread) 0px #E1306C, 10px 0 var(--spread) 0px #FD1D1D, 10px 10px var(--spread) 0px #F77737, 0 10px var(--spread) 0px #FCAF45, -10px 10px var(--spread) 0px #FFDC80;
 }
 
 /* Create the checkmark/indicator (hidden when not checked) */
 .checkmark:after {
  content: "";
  position: absolute;
  display: none;
 }
 
 /* Show the checkmark when checked */
 .container-input input:checked ~ .checkmark:after {
  display: block;
 }
 
 /* Style the checkmark/indicator */
 .container-input .checkmark:after {
  left: 0.45em;
  top: 0.25em;
  width: 0.25em;
  height: 0.5em;
  border: solid #f0f0f0;
  border-width: 0 0.15em 0.15em 0;
  transform: rotate(45deg);
 }

 .container-input {
  display: flex;
  align-items: center;
}

.label-text {
  margin-right: 15px; /* Espaçamento entre o texto e o checkbox */
}

.container-input input[type="checkbox"] {
  margin-left: 5px; /* Espaçamento à esquerda do checkbox */
}
.container-input input[type="checkbox"] {
  transform: scale(0.8); /* Reduz o tamanho da checkbox para 80% do tamanho original */
}

.gera-senha {
  display: block;
  color: white;
  background-color: black;
  border-radius: 20px;
  padding: 20px;
  align-items: center;
  justify-content: center;
  font-size: 1em;
  border: 1px;
  cursor: pointer;
}
.gera-senha:active {
  /* Estilos ao clicar no botão */
  background-color: #3e8e41;  /* Alterar a cor de fundo */
}
.gera-senha:hover {
  /* Estilos ao passar o mouse sobre o botão (hover) */
  backdrop-filter: blur(5px);  /* Alterar a cor de fundo */
}

.button {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.senha-gerada {
  margin-top: 20px;
  font-size: 30px;
  color: black;
  margin: 40px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.3s;
}

.button-container {
  display: flex;
  justify-content: center;
}

.qtd-caracteres {
  background-color: black;
  color:#f0f0f0;
  border: none;
}

.labels {
  margin-bottom: 5px;
}`, "",{"version":3,"sources":["webpack://./src/assets/css/style.css"],"names":[],"mappings":"AACA;EACE,iCAAiC;EACjC,sCAAsC;AACxC;;AAEA;EACE,sBAAsB;EACtB,UAAU;AACZ;;AAEA;EACE,SAAS;EACT,UAAU;EACV,gCAAgC;EAChC,oCAAoC;EACpC,gBAAgB;EAChB,kBAAkB;AACpB;;AAEA;EACE,gBAAgB;EAChB,iBAAiB;EACjB,gBAAgB;EAChB,aAAa;EACb,mBAAmB;EACnB,0BAA0B,EAAE,yCAAyC;EACrE,0CAA0C,EAAE,iCAAiC;EAC7E,2BAA2B,EAAE,yCAAyC;EACtE,0CAA0C,EAAE,iCAAiC;AAC/E;AACA;AACA,aAAa;AACb,QAAQ;AACR;AACA;EACE,cAAc;EACd,WAAW;EACX,mBAAmB;AACrB;;AAEA;EACE,eAAe;EACf,YAAY;EACZ,eAAe;AACjB;;AAEA;EACE,uCAAuC;AACzC;;AAEA;EACE,YAAY;EACZ,gCAAgC;EAChC,WAAW;EACX,eAAe;EACf,gBAAgB;EAChB,YAAY;EACZ,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE,uCAAuC;AACzC;;AAEA;EACE,kBAAkB;EAClB,mBAAmB;AACrB;;AAEA,8BAA8B;AAC9B;EACE,kBAAkB;EAClB,UAAU;EACV,eAAe;EACf,SAAS;EACT,QAAQ;EACR,kBAAkB;CACnB;;CAEA;EACC,cAAc;EACd,kBAAkB;EAClB,eAAe;EACf,iBAAiB;CAClB;;CAEA,6BAA6B;CAC7B;EACC,kBAAkB;EAClB,MAAM;EACN,OAAO;EACP,aAAa;EACb,YAAY;EACZ,iBAAiB;EACjB,mBAAmB;EACnB,oBAAoB;EACpB,cAAc;CACf;;CAEA,wDAAwD;CACxD;EACC,iBAAiB;EACjB,yQAAyQ;CAC1Q;;CAEA,6DAA6D;CAC7D;EACC,WAAW;EACX,kBAAkB;EAClB,aAAa;CACd;;CAEA,oCAAoC;CACpC;EACC,cAAc;CACf;;CAEA,kCAAkC;CAClC;EACC,YAAY;EACZ,WAAW;EACX,aAAa;EACb,aAAa;EACb,qBAAqB;EACrB,+BAA+B;EAC/B,wBAAwB;CACzB;;CAEA;EACC,aAAa;EACb,mBAAmB;AACrB;;AAEA;EACE,kBAAkB,EAAE,2CAA2C;AACjE;;AAEA;EACE,gBAAgB,EAAE,uCAAuC;AAC3D;AACA;EACE,qBAAqB,EAAE,6DAA6D;AACtF;;AAEA;EACE,cAAc;EACd,YAAY;EACZ,uBAAuB;EACvB,mBAAmB;EACnB,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,cAAc;EACd,WAAW;EACX,eAAe;AACjB;AACA;EACE,+BAA+B;EAC/B,yBAAyB,GAAG,2BAA2B;AACzD;AACA;EACE,oDAAoD;EACpD,0BAA0B,GAAG,2BAA2B;AAC1D;;AAEA;EACE,aAAa;EACb,uBAAuB;EACvB,gBAAgB;AAClB;;AAEA;EACE,gBAAgB;EAChB,eAAe;EACf,YAAY;EACZ,cAAc;EACd,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,iCAAiC;AACnC;;AAEA;EACE,aAAa;EACb,uBAAuB;AACzB;;AAEA;EACE,uBAAuB;EACvB,aAAa;EACb,YAAY;AACd;;AAEA;EACE,kBAAkB;AACpB","sourcesContent":["@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap');\n:root {\n  --primary-color: rgb(17, 86, 10